import Moveable from "vue-moveable";
import { mapState } from 'vuex'

export default {
  name: 'LyricsBar',

  components: {
    Moveable,
  },

  computed: {
    ...mapState('AudioPlayer', [
      'currentLyric'
    ]),
  },

  data () {
    return {
      moveable: {
        draggable: true,
      },
    }
  },

  methods: {
    handleDrag({ target, transform }) {
      target.style.transform = transform;
    },
  },
}
